@import url(https://fonts.googleapis.com/css2?family=Monsieur+La+Doulaise&family=Montserrat:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap);
*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
}

html {
  box-sizing: border-box;
  font-size: 62.5%;
  scroll-behavior: smooth;
}

body {
  font-family: sans-serif;
  font-weight: 300;
}

.App {
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.images-container {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(25rem, 1fr));
}

.images-container img {
  width: 100%;
}

@font-face {
  font-family: "Adinda Melia";
  src: url("/art-gallery/static/media/Adinda Melia.8fc231ec.otf")
    format("opentype");
}

@font-face {
  font-family: "Aerotis";
  src: url(/art-gallery/static/media/Aerotis.d1ee6a1f.otf) format("opentype");
}

.signature {
  font-family: "Aerotis", cursive;
  letter-spacing: 3px;
}

.montserrat,
.subtitle {
  font-family: "Montserrat", sans-serif !important;
}

.montserrat h1 {
  font-weight: 600;
}

.montserrat h3,
.subtitle {
  font-weight: 400;
  text-shadow: 0 0 1rem rgb(34, 34, 34);
}

.header {
  position: relative;
  width: 100%;
  height: 60vh;
  overflow-x: hidden;
  margin-bottom: 2.5rem;
  color: white;
}

.header-title {
  font-family: "Century Schoolbook", sans-serif;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  font-size: 2.5rem;
  width: 80%;
  z-index: 10;
}

.main-slide__title {
  font-size: 7rem;
}

.main-slide__subtitle {
  font-size: 3rem;
}

.header__arrow {
  position: absolute;
  top: 50%;
  width: 5rem;
  height: 5rem;
  transition: all 0.2s;
  transform: translateY(-50%) scale(0.8);
  z-index: 10;
  cursor: pointer;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.header__arrow:hover {
  transform: translateY(-50%) scale(1);
}

.header__arrow:active {
  transform: translateY(-50%) scale(0.85);
}

.header__arrow--left {
  left: 35px;
}
.header__arrow--right {
  right: 35px;
}

.slide {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 1;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
  opacity: 1;
  -webkit-animation-delay: 0s;
          animation-delay: 0s;
  -webkit-animation-duration: 0.5s;
          animation-duration: 0.5s;
  -moz-animation-timing-function: ease-out;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}

.slide.slide-left {
  -webkit-animation-name: exitLeft;
          animation-name: exitLeft;
  z-index: 1;
}

.slide.slide-right {
  -webkit-animation-name: exitRight;
          animation-name: exitRight;
  z-index: 1;
}

.slide.active.slide-left {
  -webkit-animation-name: enterLeft;
          animation-name: enterLeft;
  z-index: 2;
}

.slide.active.slide-right {
  -webkit-animation-name: enterRight;
          animation-name: enterRight;
  z-index: 2;
}

.slide__img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.slide__cover {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  /* background-image: linear-gradient(
    to bottom,
    rgba(144, 160, 255, 0.541),
    rgba(255, 255, 255, 0.521)
  ); */
  background-image: linear-gradient(
    to bottom,
    rgba(85, 85, 85, 0.671),
    rgba(255, 255, 255, 0.521)
  );
}

.dots {
  position: absolute;
  bottom: 35px;
  left: 50%;
  transform: translateX(-50%);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 10;
}

.dot {
  width: 1rem;
  height: 1rem;
  background-color: white;
  margin: 5px;
  border-radius: 100px;
  transition: all 0.5s;
}

.dot.active {
  transform: scale(1.5);
}

/* /////////////////////////////////////////////////////////////////////////////////////////////////
KEYFRAMES 
///////////////////////////////////////////////////////////////////////////////////////////////// */

@-webkit-keyframes enterLeft {
  0% {
    opacity: 0;
    transform: scale(0.5) translateX(100%);
  }
  100% {
    opacity: 1;
    transform: scale(1) translateX(0);
  }
}

@keyframes enterLeft {
  0% {
    opacity: 0;
    transform: scale(0.5) translateX(100%);
  }
  100% {
    opacity: 1;
    transform: scale(1) translateX(0);
  }
}

@-webkit-keyframes exitLeft {
  0% {
    opacity: 1;
    transform: scale(1) translateX(0);
  }
  100% {
    opacity: 0;
    transform: scale(0.5) translateX(-100%);
    visibility: hidden;
  }
}

@keyframes exitLeft {
  0% {
    opacity: 1;
    transform: scale(1) translateX(0);
  }
  100% {
    opacity: 0;
    transform: scale(0.5) translateX(-100%);
    visibility: hidden;
  }
}

@-webkit-keyframes enterRight {
  0% {
    opacity: 0;
    transform: scale(0.5) translateX(-100%);
  }
  100% {
    opacity: 1;
    transform: scale(1) translateX(0);
  }
}

@keyframes enterRight {
  0% {
    opacity: 0;
    transform: scale(0.5) translateX(-100%);
  }
  100% {
    opacity: 1;
    transform: scale(1) translateX(0);
  }
}

@-webkit-keyframes exitRight {
  0% {
    opacity: 1;
    transform: scale(1) translateX(0);
  }
  100% {
    opacity: 0;
    transform: scale(0.5) translateX(100%);
    visibility: hidden;
  }
}

@keyframes exitRight {
  0% {
    opacity: 1;
    transform: scale(1) translateX(0);
  }
  100% {
    opacity: 0;
    transform: scale(0.5) translateX(100%);
    visibility: hidden;
  }
}

.footer-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 30vh;
  width: 100%;
  background-color: rgba(145, 145, 145, 0.671);
  color: white;
  padding: 3rem 7rem;
  font-size: 1.6rem;
}

.footer-logo {
  font-family: "Century Schoolbook", sans-serif;
  font-size: 3rem;
  font-weight: 400;
}

.footer-links {
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  width: 100%;
  margin: 2rem 0 auto 0;
}

.footer-link {
  padding: 1rem 2rem;
  position: relative;
  transition: all 0.5s;
  cursor: pointer;
  border-radius: 3px;
  align-self: flex-end;
  overflow: hidden;
}

.footer-link__icon {
  width: 2.5rem;
  height: 2.5rem;
}

.footer-link::after {
  content: "";
  width: 100%;
  height: 3px;
  background-color: currentColor;
  position: absolute;
  bottom: 0;
  left: 0;
}

.footer-link:hover {
  background-color: rgba(136, 136, 136, 0.671);
  box-shadow: 0 1rem 2rem rgba(0, 0, 0, 0.2);
  transform: translateY(-6px) scale(1.2);
}

.art-container {
  padding: 2.5rem 8rem 4rem 8rem;
  /* background-image: linear-gradient(to bottom right, rgb(255, 109, 109), white); */
  /* background-size: cover; */
  /* background-position: top; */
  width: 80vw;
}

.art-title {
  font-size: 2rem;
  margin-bottom: 2.5rem;
}

.art-grid {
  width: 100%;
  background-color: transparent;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(30rem, 1fr));
  grid-auto-rows: 30rem;
  grid-gap: 4rem;
}

.art-grid__item {
  width: 100%;
  height: 100%;
  position: relative;
  overflow: hidden;
}

.art-grid__item-text {
  position: absolute;
  z-index: 1;
  bottom: -11rem;
  height: 10rem;
  width: 100%;
  background-color: rgba(255, 255, 255, 0.45);
  -webkit-backdrop-filter: blur(10px);
          backdrop-filter: blur(10px);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly;
  transition: bottom 0.5s;
}

.art-grid__item:hover .art-grid__item-text {
  box-shadow: 0 -5px 5rem rgba(0, 0, 0, 0.479);
  bottom: 0;
}

.art-grid__item-desc {
  font-size: 1.5rem;
}

.art-grid__item-author {
  font-size: 1.3rem;
}

.art-grid__item-img {
  object-fit: cover;
  width: 100%;
  height: 100%;
}

.reviews-container {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 2.5rem;
}

.header-2--white {
  font-size: 2.5rem;
  color: white;
  text-transform: uppercase;
  letter-spacing: 2px;
  margin-bottom: 3rem;
  background-image: linear-gradient(
    120deg,
    rgba(48, 48, 48, 0.541) 50%,
    rgba(122, 122, 122, 0.541) 50%
  );
  width: 100%;
  padding: 2rem 5rem;
}

.review-items-container {
  display: flex;
  justify-content: space-evenly;
  align-items: stretch;
  padding: 1rem 10rem 3rem 10rem;
}

.review-item {
  color: white;
  width: 25%;
  padding: 1.5rem;
  font-size: 2rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: rgba(32, 32, 32, 0.651);
  box-shadow: 0 0 2rem rgba(150, 150, 150, 0.603);
  border-radius: 5px;
  position: relative;
  z-index: 1;
}

.review-item__text {
  margin-bottom: 1rem;
  text-align: start;
  padding: 0 1.5rem;
  line-height: 1.2;
}

.review-item__author {
  margin-top: auto;
  margin-right: 1.5rem;
  justify-self: end;
  align-self: flex-end;
  font-size: 1.5rem;
  color: rgb(231, 231, 231);
}

.author__profession {
  display: block;
  text-align: end;
}

.features-container {
  background-color: rgba(224, 224, 224, 0.705);
  padding: 5rem 15rem 5rem 15rem;
  width: 100%;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(20rem, 1fr));
  grid-auto-rows: -webkit-max-content;
  grid-auto-rows: max-content;
  grid-column-gap: 2rem;
}

.feature-item {
  color: white;
  font-size: 2rem;
  padding: 2rem;
  background-color: rgba(83, 83, 83, 0.63);
  border-radius: 5px;
}

